.headerSize {
    font-size: 40px;
}

.statisticBox {
    padding-left: 5%;
    padding-right: 5%;
}

.underlineBlue {
    border-bottom: 2px solid #4bc0c0;
}

.underlinePurple {
    border-bottom: 2px solid #9400D3;
}

.underlineOrange {
    border-bottom: 2px solid #FF7F50;
}

.underlineBlack {
    border-bottom: 2px solid #15152B;
}

.virticalWritting {
    width: 100px;
    margin: 0 auto;
    line-height: 16px;
}

.statSubBox {
    width: 200px;
}
.loginWrapper {
    display: flex;
    height: calc(100vh - 54px);
    /* height: 100vh; */
    width: 100vw;
    justify-content: center;
    align-items: center;    
}

.loginWrapper > * {
    min-height: auto !important;
}

:root {
  --theme_color: #8fe0e7;
  --button_color: #2ca4e3;
  --button_hover_color: #4893ba;
}

html {
  font-size: 100%;
}

body {
  height: 100%;
  width: 100%;
  font-size: 1rem;
  line-height: 1.5;
  box-sizing: border-box;
}

main {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
}

.link {
  margin: 2px 5px;
}

.link:hover {
  opacity: 0.5;
}

.logout:hover {
  opacity: 0.5;
}

input {
  border: 0;
}

button:disabled {
  opacity: 0.5;
}

.content-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%;
  width: 45%;
  margin-right: 15%;
  margin-bottom: 25%;
  padding: 10px;
  position: relative;
  box-shadow: 2px 5px 5px #888888;
}

.content-box-signup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100%;
  width: 45%;
  min-width: 500px;
  margin-right: 15%;
  margin-bottom: 25%;
  padding: 10px;
  position: relative;
  box-shadow: 2px 5px 5px #888888;
  margin: auto;
}

.content-box h1 {
  font-weight: 400;
  font-size: 3em;
  margin: 0.2em 0 0.1em;
}

label {
  font-weight: 300;
  margin: 0 1px 0;
  font-size: 1.5em;
}

.content-box input {
  border-bottom: 1px solid #000;
  font-size: 1em;
  padding: 5px 1px;
  margin-top: 0.1em;
}

.contant-submit {
  background-color: #2ca4e3;
  background-color: var(--button_color);
  width: 20%;
  border-radius: 5px;
  margin-right: 5%;
}

.contant-submit:hover {
  background-color: #4893ba;
  background-color: var(--button_hover_color);
}

.flexbox {
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: stretch;
  align-content: stretch;

}

.white {
  color: #fff;
}
